.gpt3-brand-section{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    margin-top: 5rem;
    margin-bottom: 5rem;
    
}
.gpt3-brand-section div{
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 1;
    max-width: 250px;
    height: 50px;
    margin: 1rem;

}
.gpt3-brand-section div img{
    width: 100px;
}
